import React from "react";
import "./ArchedHeader.css";
import { ReactComponent as ArrowLeft } from "../../resources/svg/arrow-left.svg";
import { ReactComponent as LogoutIcon } from "../../resources/svg/logout-icon.svg";

const ArchedHeader = (props) => {
  if (props.backIcon) {
    return (
      <div className={`arched-header ${props.className && props.className}`}>
        <div
          className={`back-icon ${props.iconClassName && props.iconClassName}`}
          onClick={props.onIconClick && props.onIconClick}
        >
          <ArrowLeft />
        </div>
        {props.logoutIcon && (
          <div className="logout" onClick={props.logoutIconClick}>
            <LogoutIcon />
          </div>
        )}
      </div>
    );
  } else {
    return <div className="arched-header"></div>;
  }
};

export default ArchedHeader;
